import { useState, useEffect } from "react";
import "./count-down.css";
import { Col, Row } from "react-flexbox-grid";
import { motion } from "framer-motion";
const ountdownToWedding = ({ countDownDate }) => {
  const [days, setDays] = useState(0);
  const [hours, setHours] = useState(0);
  const [minutes, setMinutes] = useState(0);
  const [seconds, setSeconds] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      const downTime = countDownDate.getTime();
      const now = new Date().getTime();
      const distance = downTime - now;

      const daysLeft = Math.floor(distance / (1000 * 60 * 60 * 24));
      const hoursLeft = Math.floor(
        (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
      );
      const minutesLeft = Math.floor(
        (distance % (1000 * 60 * 60)) / (1000 * 60)
      );
      const secondsLeft = Math.floor((distance % (1000 * 60)) / 1000);

      setDays(daysLeft);
      setHours(hoursLeft);
      setMinutes(minutesLeft);
      setSeconds(secondsLeft);

      if (distance < 0) {
        clearInterval(interval);
      }
    }, 1000);

    return () => clearInterval(interval);
  }, [countDownDate]);

  const daysString =
    days === 1
      ? ""
      : days === 2 || days === 3 || days === 4
      ? ""
      : "";
  const hoursString =
    hours === 1
      ? ""
      : hours === 2 || hours === 3 || hours === 4
      ? ""
      : "";
  const minutesString =
    minutes === 1
      ? ""
      : minutes === 2 || minutes === 3 || minutes === 4
      ? ""
      : "";
  const secondsString =
    seconds === 1
      ? ""
      : seconds === 2 || seconds === 3 || seconds === 4
      ? ""
      : "";

  return (
    // <div style={{ }} className='count-down'>
    <div>
      <h2>  :</h2>
      <Row center="xs">
        <Col xs>
          <div>{days}</div>
          <div>{daysString}</div>
        </Col>
        <Col xs>
          <div>{hours}</div>
          <div>{hoursString}</div>
        </Col>
        <Col xs>
          <div>{minutes}</div>
          <div>{minutesString}</div>
        </Col>
        <Col xs>
          <div>{seconds}</div>
          <div>{secondsString}</div>
        </Col>
      </Row>
      {/* <div>{`${days} ${daysString}, ${hours} ${hoursString}, ${minutes} ${minutesString}, ${seconds} ${secondsString}`}</div> */}
      {/* </motion.div> */}
    </div>
  );
};

export default ountdownToWedding;
