import "./love-phrases.css";
import { useRef } from "react";
import heartsIcon from "../assets/images/icons/two-hearts.png";
import {
  motion,
  useScroll,
  useSpring,
  useTransform,
  useMotionValue,
  useVelocity,
  useAnimationFrame,
} from "framer-motion";
import { Fragment } from "react";
import { wrap } from "@motionone/utils";

const phrases = [
  " ",
  "  ,  ",
  "  ",
  "  ",
  " -  ",
  "  -  ",
  "     ",
];

function LovePhrases({ baseVelocity = -0.7 }) {
  const children = phrases.join("      ");
  const baseX = useMotionValue(0);
  const { scrollY } = useScroll();
  const scrollVelocity = useVelocity(scrollY);
  const smoothVelocity = useSpring(scrollVelocity, {
    damping: 50,
    stiffness: 400,
  });
  const velocityFactor = useTransform(smoothVelocity, [0, 1000], [0, 5], {
    clamp: false,
  });

  const x = useTransform(baseX, (v) => `${wrap(-20, -45, v)}%`);

  const directionFactor = useRef(1);
  useAnimationFrame((t, delta) => {
    let moveBy = directionFactor.current * baseVelocity * (delta / 2500);

    if (velocityFactor.get() < 0) {
      directionFactor.current = -1;
    } else if (velocityFactor.get() > 0) {
      directionFactor.current = 1;
    }

    moveBy += directionFactor.current * moveBy * velocityFactor.get();

    baseX.set(baseX.get() + moveBy);
  });

  return (
    <div
      className="parallax"
      style={{
        borderTop: "solid 1px",
        borderBottom: "1px solid",
        padding: "1em",
        // backgroundColor: "black",
      }}
    >
      <motion.div className="scroller" style={{ x, justifyContent: "center" }}>
        {Array(4)
          .fill(phrases)
          .flat()
          .map((phrase, i) => (
            <Fragment key={i}>
              <span style={{ verticalAlign: "center" }}>{phrase}</span>
              <span>
                <img
                  className="invert"
                  src={heartsIcon}
                  style={{ maxWidth: "0.9em" }}
                  alt=""
                />
              </span>
            </Fragment>
          ))}
      </motion.div>
    </div>
  );
}

export default LovePhrases;
