const CustomMap = () => {
  const styles = {
    height: "50vh",
    filter: "hue-rotate(160deg) grayscale(30%)",
    borderRadius: "16px",
  };
  const mapRef = useRef(null);
  const ymaps = useYMaps(["Map"]);

  useEffect(() => {
    if (!ymaps || !mapRef.current) {
      return;
    }

    let map = new ymaps.Map(mapRef.current, {
      center: [56.818415, 60.641663],
      zoom: 15,
    });
    map.behaviors.disable("drag");

    // map.controls.add(ymaps.FullscreenControl)
    // ({
    //   geometry: [[56.818574, 60.641048], [56.818600, 60.640837], [56.818278, 60.640732], [56.818164, 60.641029], [56.818012, 60.642113], [56.817846, 60.642057], [56.817826, 60.641886], [56.818055, 60.640136], [56.818101, 60.639212]],
    //   options:{strokeWidth: 4}
    // })
  }, [ymaps]);

  return <div ref={mapRef} style={styles} />;
};

